body {
    font-family: Arial, sans-serif;
    background-color: #141414;
    color: #fff;
    text-align: center;
    margin: 0;
    box-sizing: border-box;
    padding: 20px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    cursor: none;
}

h1 {
    color: #fff;
    font-size: 3rem;
}

button {
    background-color: #007bff;
    font-size: 1.25rem;
    color: #fff;
    border: none;
    padding: 10px 20px;
    cursor: pointer;
    margin-top: 10px;
    border-radius: 5px;
    transition: .2s;

    &:hover {
        background-color: #0056b3;
    }
}

p {
    margin-top: 20px;
    font-size: 18px;
    color: #555;
}

.container {
    display: flex;
    gap: 20px;
}

.image-wrapper {
    position: relative;

    img {
        display: block;
    }
}

.cursor {
    position: fixed;
    pointer-events: none;
    z-index: 1000;
    transition: opacity 0.3s ease, transform 0.3s ease;
}

.cursor__dot {
    width: 18px;
    height: 18px;
    border-radius: 50%;
    background-color: #e92b2b;
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
    opacity: 1;
    transition: width 0.3s ease, height 0.3s ease;
}

.cursor__text {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%) scale(0, 0);
    text-align: center;
    font-size: 16px;
    text-transform: uppercase;
    color: #fff;
    opacity: 0;
    transition: opacity 0.3s ease, transform 0.3s ease;
}

.cursor.large .cursor__dot {
    width: 72.5px;
    height: 72.5px;
}

.cursor.large .cursor__text {
    opacity: 1;
    transform: translate(-50%, -50%) scale(1, 1);
}

@media screen and (max-width: 1100px) {
    body {
        cursor: auto;
    }

    .cursor {
        display: none
    }
}